<template>
	<view class="container">
		<u-parse :html="info.content"></u-parse>
	</view>
</template>

<script>
	import uParse from "@/components/u-parse/u-parse.vue";
	export default {
		data() {
			return {
				info:{}
			}
		},
		onLoad() {
			this.getInfo()
		},
		components: {
			uParse
		},
		methods: {
			async getInfo() {
				let res = await this.$u.get('/v2/info/index', {
					sign: 'yinsi'
				})
				this.info = res.data
			},
		}
	}
</script>

<style scoped>
	page {
		background-color: #F5F5F7;
	}

 
</style>
